.display-container {
  // background-color: red;
  max-height: 50vh;
  overflow: hidden;
}
.grid {
  // display: none;
  width: 100%;
  // background-color: green;

  animation: slideUp 26s ease-in-out infinite;

  &:hover {
    animation-play-state: paused;
  }
}

.grid-item {
  float: left;
  img {
    display: block;
    max-width: 100%;
  }
}

.grid-sizer,
.grid-item {
  width: calc(34% - 9px);
  margin-bottom: 9px;
  background-size: cover;
}
/* 2 columns wide */
.grid-item--width2 {
  width: calc(66% - 9px);
}
/* clear fix */
.grid:after {
  content: '';
  display: block;
  clear: both;
}

@keyframes slideUp {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-75%);
  }
}
